.button {
    margin: 10px;
    padding: 10px 20px;
    font-size: 16px;
    border: 1px solid #363636;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(2, 2, 2, 0.2);
    cursor: pointer;
    transition: background-color 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.button:hover {
    background-color: #737774;
    color: #0a0d0e;
}